haverun = 0
setTimeout(() => {

    window.onload()

}, 1000)
window.onload = function () {

    if (haverun == 0) {
        haverun = 1
        console.log("开始运行此onload函数")
        myonloadfunc()
    }
    else {
        console.log("不要重复运行此onload函数")
    }

}

